.section-projects {
    &__title {
        padding-top: 190px;
        text-align: center;
    }
    &__line {
        margin: 0 auto;
        margin-bottom: 135px;
    }
    &__btn {
        display: inline-block;
        text-align: center;
        margin: 0 auto;
        margin-bottom: 115px;
    }
}

.projects-gallery {
    cursor: pointer;
    width: 200%;
    margin-bottom: 50px;
    display: flex;
}

.projects-gallery:hover {
    will-change: transform;
}

.projects-gallery-item {
    font-size: 0;
    height: 275px;
    margin-right: 45px;
    pointer-events: none;
}

.active-image {
    transform: scaleY(1.2);
    transform-origin: 100%;
}