.footer {
    position: relative;
    height: 10vw;
    background-color: $black;
    width: 100vw;
}

.footer-logo {
    left: 14vw;
}

.rights {
    position: absolute;
    top: 6.6875vw;
    left: 14vw;
    color: #7b7b7b;
}

.footer-nav {
    position: absolute;
    left: 67.3125vw;
    top: 3.25vw;
    &__list {}
    &__item {
        margin-right: 2.5vw;
        display: inline-block;
    }
    &__link {
        color: $white;
        font-size: 0.875vw;
        text-transform: uppercase;
    }
}