.section-reviews {
    padding-bottom: 56%;
    position: relative;
    background-size: cover;
}

.reviews-slider {
    position: absolute;
    z-index: 1;
    top: 23vw;
    left: 16.75vw;
    width: 67.875vw;
}

.reviews-slider-item {
    position: relative;
    z-index: 10;
    width: 48.3175vw;
    margin: auto;
    &__description {
        text-align: center;
        display: block;
        font-family: $playfair-italic;
        color: $white;
        font-size: 2.25vw;
        line-height: 3vw;
        margin-bottom: 2.1875vw;
    }
    &__line {
        width: 8.1vw;
        height: 2px;
        background-color: $gold;
        margin: auto;
        margin-bottom: 1.87vw;
    }
    &__author {
        display: block;
        color: $white;
        text-align: center;
        font-size: 1.875vw;
        margin-bottom: 0.9375vw;
    }
    &__auto {
        display: block;
        color: $white;
        text-align: center;
        font-size: 1.125vw;
    }
}

.reviews-slider-arrow {
    position: absolute;
    top: 5%;
    width: 20px;
    height: 20px;
    svg {
        font-size: 2rem;
        width: 100%;
        height: 100%;
    }
    &_left {
        left: -3%;
    }
    &_right {
        right: -3%;
    }
}