    @import '../mixins';
    @import '../variables';
    .heading {
        position: relative;
        height: 100vh;
        width: 100%;
    }
    
    .heading-bg {
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        will-change: transform;
    }
    
    .logo-label {
        color: $white;
    }
    
    .logo {
        position: absolute;
        top: 2.75vw;
        left: 40px;
        display: flex;
    }
    
    .logo-icon {
        margin-right: 10px;
        svg {
            width: 40px;
            height: 40px;
        }
    }
    
    .logo-label {
        &__item {
            display: block;
            text-transform: uppercase;
            &_up {
                font-family: $playfair-bold;
                font-size: 20px;
                margin-bottom: 5px;
            }
            &_down {
                font-size: 11px;
            }
        }
    }
    
    .nav {
        position: absolute;
        top: 40px;
        left: 35%;
        display: flex;
    }
    
    .nav-item {
        margin-right: 35px;
    }
    
    .roll-hover {
        display: block;
        height: 20px;
        overflow: hidden;
        text-transform: uppercase;
        &__label {
            color: $white;
            line-height: 20px;
            transform: translate3d(0, 0, 0);
            transition: transform .4s cubic-bezier(.41, .15, .13, .87);
        }
    }
    
    .roll-hover:hover .roll-hover__label {
        transform: translate3d(0, -20px, 0);
    }
    
    .menu {
        position: absolute;
        top: 40px;
        right: 40px;
        display: flex;
        align-items: center;
    }
    
    .hamburger {
        position: relative;
        width: 14px;
        height: 11px;
        margin-right: 10px;
        &__item {
            position: absolute;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: $gold;
            &_1 {
                top: 0;
            }
            &_2 {
                top: 50%;
            }
            &_3 {
                top: 92%;
            }
        }
    }
    
    .menu-label {
        position: relative;
        top: 1px;
    }
    
    .heading-content {
        position: absolute;
        z-index: 1;
        top: 20%;
        left: 35%;
        display: none;
        transform: translate3d(0, 0, 0);
    }
    
    .heading-content-title {
        color: $gold;
        font-size: 12rem;
        font-family: $playfair-bold;
        margin-bottom: 40px;
        transform: translate3d(0, 0, 0);
    }
    
    .heading-content-title-label {
        overflow: hidden;
        padding-bottom: 15px;
        &__item {
            display: block;
            transform: translate3d(0, 120px, 0);
        }
    }
    
    .heading-content-description {
        color: $white;
        font-size: 1.6rem;
        line-height: 24px;
        transform: translate3d(0, 0, 0);
    }
    
    .heading-content-description-label {
        overflow: hidden;
        &__item {
            display: block;
            transform: translate3d(0, 18px, 0);
        }
    }
    
    .social-media {
        position: absolute;
        bottom: 40px;
        display: flex;
        left: 20px;
        &__link {
            display: inline-block;
            color: $white;
            padding: 20px;
            bottom: 25px;
        }
    }
    
    .counter {
        position: absolute;
        left: 35%;
        bottom: 45px;
        display: flex;
        align-items: center;
        &__number {
            color: $white;
            font-size: 2.6rem;
            font-family: $playfair-bold;
        }
        &__line {
            position: relative;
            height: 1px;
            background-color: $gray;
            width: 165px;
            margin: 0 20px;
            overflow: hidden;
            &_active {
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                transform: translateX(-100%);
                background-color: $white;
            }
        }
    }
    
    .scroll-label {
        cursor: pointer;
        position: absolute;
        right: 40px;
        bottom: 45px;
        color: $white;
    }
    
    .active-content {
        display: block;
    }
    
    .scroll-line {
        position: fixed;
        top: -100%;
        right: 0;
        width: 7px;
        height: 100%;
        background-color: $black;
        z-index: 100;
        transform: translate3d(0, 0, 0);
        &_overlay {
            z-index: 99;
            top: 0;
            background-color: $white;
        }
    }