*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;
}

body {
    font-family: $proxima-regular;
    font-size: 1.4rem;
    overflow: hidden;
}

a {
    text-decoration: none;
    color: inherit;
}

.main-layout {
    position: relative;
    overflow: hidden;
}

.section-wrapper {
    max-width: 1200px;
    margin: 0 auto;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    @include breakpoint(1400px) {
        margin-left: 5%;
        margin-right: 5%;
    }
}

.title {
    font-family: $playfair-bold;
    font-size: 6rem;
    margin-bottom: 25px;
}

.line {
    width: 50px;
    height: 2px;
    background-color: $gold;
    margin-bottom: 30px;
}

.main-layout {
    will-change: transform;
    transform: translate3d(0, 0, 0);
}