.section-about {
    position: relative;
    z-index: 3;
    background-color: $white;
}

.about-inline {
    display: flex;
    align-items: center;
    padding: 125px 0;
}

.image-wrapper {
    width: 55%;
    height: 545px;
}

.image {
    object-fit: cover;
    max-width: 100%;
    height: 100%;
}

.content {
    width: 45%;
    padding: 0 100px;
}

.description {
    line-height: 24px;
    font-size: 16px;
    margin-bottom: 40px;
}

.btn {
    position: relative;
    text-transform: uppercase;
    display: inline-block;
    padding: 18px 73px;
    border: 2px solid $gold;
    overflow: hidden;
    transition: color .2s ease-out;
}

.btn:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    top: 0;
    left: 0;
    border: 2px solid $gold;
    background-color: $gold;
    transform: translate3d(0, 100%, 0);
    transition: transform .4s ease-in-out;
}

.btn:hover:before {
    transform: translate3d(0, 0, 0);
}

.center-btn {
    display: flex;
    justify-content: center;
}

.btn:hover {
    color: $white;
}