        .section-services {
            height: 530px;
            &__title {
                color: $white;
                padding-top: 110px;
            }
            &__line {
                margin-bottom: 55px;
            }
        }
        
        .services-gallery {
            display: flex;
            max-width: 1140px;
            margin-left: 60px;
        }
        
        .services-gallery-item {
            position: relative;
            overflow: hidden;
            width: 350px;
            height: 348px;
            &__image {
                position: absolute;
                top: 0;
                left: 0;
            }
            &__overlay {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: $black;
                opacity: 0.5;
                transition: opacity .3s ease-out;
            }
            &__content {
                padding-top: 90px;
                position: relative;
                max-width: 210px;
                margin: 0 auto;
                z-index: 10;
                display: flex;
                flex-direction: column;
                align-items: center;
                transform: translate3d(0, 220px, 0);
                transition: transform .4s ease-out;
            }
            &__border {
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                border: 2px solid $gold;
                opacity: 0;
                transform: translate3d(0, 0, 0);
                transition: all .2s ease-out;
            }
        }
        
        .services-gallery-item:not(:nth-last-child(1)) {
            margin-right: 47px;
        }
        
        .overlay-title {
            color: $white;
        }
        
        .gallery-item-title {
            color: $white;
            text-align: center;
            font-size: 24px;
            margin-bottom: 17px;
        }
        
        .line-sm {
            width: 35px;
            height: 2px;
            background-color: $gold;
            margin: 0 auto;
            margin-bottom: 22px;
        }
        
        .gallery-item-description {
            color: $white;
            text-align: center;
            line-height: 24px;
            margin-bottom: 37px;
        }
        
        .gallery-item-link {
            display: inline-block;
            margin: 0 auto;
            color: $gold;
            text-align: center;
            text-transform: uppercase;
            border-bottom: 1px solid $gold;
        }
        
        .services-gallery-item:hover .services-gallery-item__content {
            transform: translate3d(0, 0, 0);
        }
        
        .services-gallery-item:hover .services-gallery-item__overlay {
            opacity: 0.9;
        }
        
        .services-gallery-item:hover .services-gallery-item__border {
            opacity: 1;
            transform: scale(0.9);
        }